var test = require('ava');
var app = require('../app');
var request = require('supertest-promised')(app);

test.serial('wrong pass', function (t) {
  return request.post('/')
    .send({
      "host": "197.211.242.121",
      "user": "1",
      "pass": "1"
    })
    .expect(400)
    .expect(function (res) {
      t.is(JSON.parse(res.body.error).text, 'Connection success');
    });
});

test.serial('success open', function (t) {
  return request.post('/')
    .send({
      "host": "197.211.242.121",
      "user": "1",
      "pass": "1"
    })
    .expect(200)
    .expect(function (res) {
      t.is(res.body.status, 'Connection success');
    });
});
